'use client'

import { useState, useEffect } from 'react'
import { database } from '@/lib/firebase'
import { ref, onValue } from 'firebase/database'
import type { Product, Category } from '@/types'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Badge } from '@/components/ui/badge'
import { ProductCard } from '@/components/products/product-card'
import { Search, Filter, Package, ShoppingBag, ArrowLeft, Grid, List } from 'lucide-react'
import Link from 'next/link'
import { useSearchParams } from 'next/navigation'

const CategoriesPage = () => {
  const [products, setProducts] = useState<Product[]>([])
  const [categories, setCategories] = useState<Category[]>([])
  const [filteredProducts, setFilteredProducts] = useState<Product[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState<string>('all')
  const [sortBy, setSortBy] = useState<string>('newest')
  const [priceRange, setPriceRange] = useState<string>('all')
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid')
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const searchParams = useSearchParams()

  useEffect(() => {
    const categoryFromUrl = searchParams.get('category')
    if (categoryFromUrl) {
      setSelectedCategory(categoryFromUrl)
    }
  }, [searchParams])

  useEffect(() => {
    console.log('Setting up Firebase listeners for categories page...')
    try {
      // Load products
      const productsRef = ref(database, 'products')
      const unsubscribeProducts = onValue(productsRef, (snapshot) => {
        try {
          const data = snapshot.val()
          if (data) {
            const productList = Object.entries(data)
              .map(([id, product]: [string, any]) => ({
                id,
                ...product,
                name: product?.name || '',
                description: product?.description || '',
                category: product?.category || '',
                price: product?.price || 0,
                stock: product?.stock || 0,
                imageUrl: product?.imageUrl || '',
                sellerId: product?.sellerId || '',
                sellerName: product?.sellerName || '',
                colors: product?.colors || [],
                sizes: product?.sizes || [],
                createdAt: product?.createdAt || Date.now()
              }))
              .filter(product => product.name && product.category)
              .sort((a, b) => b.createdAt - a.createdAt)
            
            setProducts(productList)
          }
        } catch (err) {
          console.error('Error processing products:', err)
        }
      })

      // Load categories
      const categoriesRef = ref(database, 'categories')
      const unsubscribeCategories = onValue(categoriesRef, (snapshot) => {
        try {
          const data = snapshot.val()
          if (data) {
            const categoryList = Object.entries(data).map(([id, category]: [string, any]) => ({
              id,
              ...category,
              name: category?.name || '',
              description: category?.description || '',
              imageUrl: category?.imageUrl || ''
            }))
            setCategories(categoryList)
          }
        } catch (err) {
          console.error('Error processing categories:', err)
        } finally {
          setLoading(false)
        }
      })

      return () => {
        unsubscribeProducts()
        unsubscribeCategories()
      }
    } catch (err) {
      console.error('Error setting up Firebase listeners:', err)
      setError('Failed to load data')
      setLoading(false)
    }
  }, [])

  useEffect(() => {
    try {
      let filtered = [...products]

      // Filter by category
      if (selectedCategory && selectedCategory !== 'all') {
        filtered = filtered.filter(product => {
          const productCategory = product?.category || ''
          const selectedCat = selectedCategory || ''
          return productCategory.toLowerCase() === selectedCat.toLowerCase()
        })
      }

      // Filter by search term
      if (searchTerm && searchTerm.trim()) {
        const searchLower = searchTerm.toLowerCase().trim()
        filtered = filtered.filter(product => {
          const name = (product?.name || '').toLowerCase()
          const description = (product?.description || '').toLowerCase()
          const category = (product?.category || '').toLowerCase()
          const sellerName = (product?.sellerName || '').toLowerCase()
          
          return name.includes(searchLower) || 
                 description.includes(searchLower) || 
                 category.includes(searchLower) ||
                 sellerName.includes(searchLower)
        })
      }

      // Filter by price range
      if (priceRange !== 'all') {
        filtered = filtered.filter(product => {
          const price = product.price || 0
          switch (priceRange) {
            case 'under-500':
              return price < 500
            case '500-1000':
              return price >= 500 && price <= 1000
            case '1000-2000':
              return price >= 1000 && price <= 2000
            case 'above-2000':
              return price > 2000
            default:
              return true
          }
        })
      }

      // Sort products
      switch (sortBy) {
        case 'price-low':
          filtered.sort((a, b) => (a.price || 0) - (b.price || 0))
          break
        case 'price-high':
          filtered.sort((a, b) => (b.price || 0) - (a.price || 0))
          break
        case 'name':
          filtered.sort((a, b) => (a.name || '').localeCompare(b.name || ''))
          break
        case 'newest':
        default:
          filtered.sort((a, b) => (b.createdAt || 0) - (a.createdAt || 0))
          break
      }

      setFilteredProducts(filtered)
    } catch (err) {
      console.error('Error filtering products:', err)
      setFilteredProducts(products)
    }
  }, [products, searchTerm, selectedCategory, sortBy, priceRange])

  const clearFilters = () => {
    setSearchTerm('')
    setSelectedCategory('all')
    setPriceRange('all')
    setSortBy('newest')
  }

  const getSelectedCategoryName = () => {
    if (selectedCategory === 'all') return 'All Products'
    const category = categories.find(cat => cat.name === selectedCategory)
    return category?.name || selectedCategory
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-orange-600"></div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <Package className="h-16 w-16 text-gray-400 mx-auto mb-4" />
          <h2 className="text-xl font-semibold text-gray-900 mb-2">Something went wrong</h2>
          <p className="text-gray-600 mb-4">{error}</p>
          <Button onClick={() => window.location.reload()}>
            Try Again
          </Button>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-4">
            <Link href="/">
              <Button variant="ghost" size="sm">
                <ArrowLeft className="h-4 w-4 mr-2" />
                Back to Home
              </Button>
            </Link>
            <div>
              <h1 className="text-2xl md:text-3xl font-bold text-gray-900">
                {getSelectedCategoryName()}
              </h1>
              <p className="text-gray-600 mt-1">
                {filteredProducts.length} product{filteredProducts.length !== 1 ? 's' : ''} found
              </p>
            </div>
          </div>
          
          <div className="flex items-center gap-2">
            <Button
              variant={viewMode === 'grid' ? 'default' : 'outline'}
              size="sm"
              onClick={() => setViewMode('grid')}
            >
              <Grid className="h-4 w-4" />
            </Button>
            <Button
              variant={viewMode === 'list' ? 'default' : 'outline'}
              size="sm"
              onClick={() => setViewMode('list')}
            >
              <List className="h-4 w-4" />
            </Button>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
          {/* Sidebar Filters */}
          <div className="lg:col-span-1">
            <div className="bg-white rounded-lg shadow-sm p-6 sticky top-6">
              <h3 className="text-lg font-semibold text-gray-900 mb-4">Filters</h3>
              
              {/* Search */}
              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Search Products
                </label>
                <div className="relative">
                  <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    type="text"
                    placeholder="Search..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="pl-10"
                  />
                </div>
              </div>

              {/* Categories */}
              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Categories
                </label>
                <div className="space-y-2">
                  <Button
                    variant={selectedCategory === 'all' ? 'default' : 'ghost'}
                    className="w-full justify-start"
                    onClick={() => setSelectedCategory('all')}
                  >
                    All Products
                    <Badge variant="secondary" className="ml-auto">
                      {products.length}
                    </Badge>
                  </Button>
                  {categories.map((category) => {
                    const categoryProducts = products.filter(p => 
                      p.category.toLowerCase() === category.name.toLowerCase()
                    )
                    return (
                      <Button
                        key={category.id}
                        variant={selectedCategory === category.name ? 'default' : 'ghost'}
                        className="w-full justify-start"
                        onClick={() => setSelectedCategory(category.name)}
                      >
                        {category.name}
                        <Badge variant="secondary" className="ml-auto">
                          {categoryProducts.length}
                        </Badge>
                      </Button>
                    )
                  })}
                </div>
              </div>

              {/* Price Range */}
              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Price Range
                </label>
                <div className="space-y-2">
                  {[
                    { value: 'all', label: 'All Prices' },
                    { value: 'under-500', label: 'Under ₹500' },
                    { value: '500-1000', label: '₹500 - ₹1,000' },
                    { value: '1000-2000', label: '₹1,000 - ₹2,000' },
                    { value: 'above-2000', label: 'Above ₹2,000' }
                  ].map((range) => (
                    <Button
                      key={range.value}
                      variant={priceRange === range.value ? 'default' : 'ghost'}
                      className="w-full justify-start"
                      onClick={() => setPriceRange(range.value)}
                    >
                      {range.label}
                    </Button>
                  ))}
                </div>
              </div>

              {/* Sort By */}
              <div className="mb-6">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Sort By
                </label>
                <div className="space-y-2">
                  {[
                    { value: 'newest', label: 'Newest First' },
                    { value: 'price-low', label: 'Price: Low to High' },
                    { value: 'price-high', label: 'Price: High to Low' },
                    { value: 'name', label: 'Name A-Z' }
                  ].map((sort) => (
                    <Button
                      key={sort.value}
                      variant={sortBy === sort.value ? 'default' : 'ghost'}
                      className="w-full justify-start"
                      onClick={() => setSortBy(sort.value)}
                    >
                      {sort.label}
                    </Button>
                  ))}
                </div>
              </div>

              <Button onClick={clearFilters} variant="outline" className="w-full">
                Clear All Filters
              </Button>
            </div>
          </div>

          {/* Products Grid */}
          <div className="lg:col-span-3">
            {filteredProducts.length === 0 ? (
              <div className="text-center py-12">
                <ShoppingBag className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                <h3 className="text-xl font-semibold text-gray-900 mb-2">No products found</h3>
                <p className="text-gray-600 mb-4">
                  Try adjusting your search or filters
                </p>
                <Button onClick={clearFilters}>
                  Clear Filters
                </Button>
              </div>
            ) : (
              <div className={
                viewMode === 'grid' 
                  ? "grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6"
                  : "space-y-4"
              }>
                {filteredProducts.map((product) => (
                  <ProductCard 
                    key={product.id} 
                    product={product} 
                    compact={viewMode === 'grid'} 
                    square={viewMode === 'grid'}
                  />
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default CategoriesPage
